<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="campaign_id"
            :HeaderText="$t('SMSCampaign')" :listType="['List']" :ListData="CampaignData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" 
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <modalAddSMSCampaign v-if="ShowmodalAddSMSCampaign" @close="ShowmodalAddSMSCampaign = false"></modalAddSMSCampaign>
    </section>
</template>

<script>
import modalAddSMSCampaign from '../../../../components/Common/Modal/modalAddSMSCampaign.vue';
import Schema from '../SMSCampaign/SMSCampaignSchema'
export default{
    components:{
        modalAddSMSCampaign
    },
    data() {
        return {
            ShowmodalAddSMSCampaign: false,
            isLoading: false,
            CampaignData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecords: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            searchCondition: '',
            Headers: [
                {
                    "COLUMN_NAME": "name",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Name",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "date",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Description",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "type",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Type",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "attachment",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Attachment",
                    "DISPLAY_ORDER": 4
                }
            ]
        };
    },
    created() {
        this.CampaignData = [{
                "rownumber": 1,
                "total_records": 125,
                "campaign_id": 572,
                "name": "Noise watch",
                "description": "Dave Junior",
                "type": "SMS",
                "attachment": "2",
            }];
    },
    methods: {
        commonHeaderButtonClick: function (event) {
            debugger;
            switch (event.callbackfunction) {
                case "modalAddSMSCampaign":
                    this.modalAddSMSCampaign();
                    break;
            }
        },
        modalAddSMSCampaign: function () {
            this.ShowmodalAddSMSCampaign = true;
        },
        ClosemodalAddSMSCampaign: function () {
            this.ShowmodalAddSMSCampaign = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }
    },
}
</script>