<template lang="en">
<section>
    <div class="modal my-popups right fade show" role="dialog" style="display: block;">
        <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog mw-1100" role="document">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" @click="closepopup"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 id="myModalLabel" class="modal-title">{{ $t("ComposeSMS") }}</h4>
                </div>
                <div class="modal-body">
                    <div class="col-sm-12 col-md-6 p-0 float-right bg-light p-4">
                        <div class="mobilebase mx-auto">
                            <div class="col-12 bg-light p-2 border"><em class="fa fa-bars text-secondary"></em></div>
                            <div class="col-12 p-2 bg-light border" style="min-height:350px;">
                                <div class="col-12" id="textcontent">Dear {{ msgcustomvalue }} Please follow the given link {{ msgtriggervalue }} to access the website.</div>
                            </div>
                        </div>                                                                                          
                    </div>
                    <dynamic-form :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-Send_Test="{data}">
                            <div class="col-12 float-left mt-3">
                                <div class="input-group">
                                    <span class="w-100 d-flex">
                                        <input type="text" placeholder="Enter Mobile Number" v-model="MobileNumber" :class="data.error != '' ? 'form-control w-70 mr-2 is-invalid':'form-control w-70 mr-2'" autocomplete="off" />
                                    </span>
                                    <span class="invalid-feedback d-block" v-if="data.error != ''">{{ data.error }}</span>
                                </div>
                            </div>
                        </template>
                    </dynamic-form>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

    
<script>
import CampaignDataServices from "../../../services/DataService";

export default {
    data() {
        return {
            CustomValue: [],
            TiggerData: [],
            modelcustomvalue: '',
            modeltriggervalue: '',
            msgcustomvalue: '',
            msgtriggervalue: '',
            MobileNumber: '',
            MobSMS:'Dear ' + this.msgcustomvalue + ' Please follow the given link ' + this.msgtriggervalue + ' to access the website.',
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('name'),
                        name: "name",
                        value: "",
                        placeholder: "Enter Campaign Name",
                        config: {},
                        validationRules: { "required": true }
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('CustomValue'),
                            name: "CustomValue",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onSetvalue
                            },
                            validationRules: { "required": true }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('TriggerLink'),
                            name: "TriggerLink",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onSetvalue
                            },
                            validationRules: { "required": true }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [ {
                            astype: "FileField",
                            label: this.$t('FileControlText'),
                            name: "ChooseFile",
                            value: "",
                            placeholder: "Select",
                            config: {
                                maxSize:5,
                                accept:[
                                    {
                                       fileType:'png',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpeg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'pdf',
                                       iconClass:'fa-file-archive-o'
                                    }
                                ],
                                // showSelectedFiles: [
                                //     {
                                //         attachement_name: "",
                                //         attachement_path: "",
                                //         attachement_mime: "",
                                //         created_by: "",
                                //     },
                                // ],
                                options: [],
                                showAddIcon: true,
                            },
                            validationRules: { "required": false }
                        },
                        
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                            astype: "SlotField",
                            label: this.$t('MobileNumber'),
                            name: "Send_Test",
                            value: "",
                            placeholder: "Enter Mobile Number",
                            config: {
                                options: [],
                            },
                            validationRules: {"required": true},
                            visibility: true,
                        },
                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onclick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closepopup,
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "Send",
                    onClick: this.SendSMS,
                    isVisible: true,
                },
            ],
        }
    },
    created() {
        var vm = this;
        vm.GetCustomFieldValue();
        vm.GetTriggerFieldValue();
    },
    methods: {
        closepopup() {
            var vm = this;
            vm.$parent.ClosemodalAddSMSCampaign();
        },
        GetCustomFieldValue() {
            var vm = this;
            var url = 'MODULE_NAME=CRM&SUB_MODULE_CODE=CRM_CAMPAIGNS,CRM_CONTACTS';
            CampaignDataServices.GetCustomFieldValue(url).then(function (response) {
                vm.CustomValue = response.data;
                vm.CustomValue.forEach(item=>{
                    vm.FormSchema[1].Data[0].config.options.push({
                        name: item.DISPLAY_NAME,
                        value: item.CUSTOM_FIELD_ID
                    })
                })
            });
        },
        GetTriggerFieldValue() {
            var vm = this;
            var url = 'TYPE=CRM_TRIGGER&MODULE_NAME=null';
            CampaignDataServices.GetTriggerFieldValue(url).then(function (response) {
                vm.TiggerData = response.data;
                vm.TiggerData.forEach(item=>{
                    vm.FormSchema[1].Data[1].config.options.push({
                        name: item.NAME,
                        value: item.ID
                    })
                })
            })
        },
        onSetvalue() {
            var vm = this;
            if (vm.FormSchema[1].Data[0].value != '') {
                var getcustomvalue = vm.CustomValue.find(x => x.CUSTOM_FIELD_ID === vm.FormSchema[1].Data[0].value);
                if (getcustomvalue != undefined) {
                    vm.msgcustomvalue = getcustomvalue.DISPLAY_NAME;
                }
            }
            if (vm.FormSchema[1].Data[1].value != '') {
                var getriggervalue = vm.TiggerData.find(x => x.ID === vm.FormSchema[1].Data[1].value);
                if (getriggervalue != undefined) {
                    vm.msgtriggervalue = getriggervalue.NAME;
                }
            }
        },
        SendSMS() {
            var vm = this;
            vm.MobSMS = 'Dear ' + vm.msgcustomvalue + ' Please follow the given link ' + vm.msgtriggervalue + ' to access the website.';
            var obj = {
                Send_sms: vm.MobSMS,
                Send_to: vm.MobileNumber,
            }
            var url = JSON.stringify(obj);
            CampaignDataServices.SendTwilioSMS(url).then(function(response){
                debugger;
            })
        },
        onSubmit(formfields, val){
        debugger;
            var vm = this;
            vm.MobSMS = 'Dear ' + vm.msgcustomvalue + ' Please follow the given link ' + vm.msgtriggervalue + ' to access the website.';
            var obj ={
                name: formfields.name,
                Choose_File: formfields.ChooseFile,
                MobSMS: vm.MobSMS,    
            }
            var url = JSON.stringify(obj);
            CampaignDataServices.SaveAddSMSCampaign(url).then(function(response){
                debugger;
            })
        }
    }
}
</script>
